<template>
    <div style="width: calc(100vw);height: calc(100vh);display: flex;justify-content: center;align-items: center;">
        <div id="netFull" class="chart-container" style="width: calc(70vw);height: calc(70vh);background: #F6F7FB;"></div>
    </div>
</template>
<script type="text/javascript" src="/static/js/echarts.js"></script>
<script>
// import NetGraph from "@/components/NetGraph.vue";
  export default {
    name: "PopperVue",
    data() {
        return {
            chart: undefined
        }
    },
    methods: {
        clear() {
            if (this.chart != undefined) this.chart.clear()
        },
        show(newOption, roam) {
            var chart = echarts.init(
                document.getElementById('netFull'), 'white', {renderer: 'canvas'});
                chart.clear()
                chart.setOption(newOption);
                let option = chart.getOption()
                if (!roam) return
                // console.log(option)
                let origin_font_size = new Array(option.series[0].data.length)
                for (var i = 0;i < option.series[0].data.length; i++) {
                origin_font_size[i] = option.series[0].data[i].label.fontSize
            }

                // console.log(origin_font_size)
                chart.getZr().on("mousewheel", function () {
                let option = chart.getOption()
                let zoom = option.series[0].zoom;
                // console.log(zoom)
                for (var i = 0;i < option.series[0].data.length; i++) {
                    option.series[0].data[i].label.fontSize = Math.ceil(origin_font_size[i] * zoom)
                }
                chart.clear()
                chart.setOption(option,true);  this.chart = chart})
        }
    }
};
</script>