    },
    openEdgeInfo(source, target, value, blocknumber, tx_hash) {
            this.$refs.EdgeInfo.open(source, target, value, blocknumber, tx_hash)
        },
    go(event, name) {
      if (this.$refs.router.timer != undefined) {
        for (var i = 0; i < this.$refs.router.timer.length; i++)
        clearInterval(this.$refs.router.timer[i])
      }
      this.$router.push({name: name, params: {history: this.historyData}})
