<!-- Table -->
<template>
  <div>
    <el-dialog title="Link Detail" :visible.sync="dialogVisible">
      <el-collapse v-model="activeName" accordion>
        <el-collapse-item title="Source" name="1">
          <div class="info-block">
            <el-tag class="info-tag">Address:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.source.address }}</el-tag>
          </div>
          <div class="info-block">
            <el-tag class="info-tag">Degree:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.source.degree }}</el-tag>
          </div>
        </el-collapse-item>
        <el-collapse-item title="target" name="2">
          <div class="info-block">
            <el-tag class="info-tag">Address:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.target.address }}</el-tag>
          </div>
          <div class="info-block">
            <el-tag class="info-tag">Degree:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.target.degree }}</el-tag>
          </div>
        </el-collapse-item>
        <el-collapse-item title="BlockInfo" name="3">
          <div class="info-block">
            <el-tag class="info-tag">BlockNumber:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.blocknumber }}</el-tag>
          </div>
          <div class="info-block">
            <el-tag class="info-tag">tx_hash:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.tx_hash }}</el-tag>
          </div>
        </el-collapse-item>
        <el-collapse-item title="Value" name="4">
          <div class="info-block">
            <el-tag class="info-tag">Value:</el-tag>
            <el-tag type="success" class="info-detail">{{ this.value }}</el-tag>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'EdgeInfo',
    data() {
      return {
        activeName: '1',
        dialogVisible: false,
        source: undefined,
        target: undefined,
        value: undefined,
        blocknumber: undefined,
        tx_hash: undefined
      }
    },
    methods: {
      open(source, target, value, blocknumber, tx_hash) {
        this.source = source
        this.target = target
        this.value = value
        this.blocknumber = blocknumber
        this.tx_hash = tx_hash
        this.dialogVisible = true
      }
    }
  };
</script>
<style>
  .info-block {
    width: 100%;
    display: flex;
    margin-bottom: 2%
  }
  .info-tag {
    min-width: 10%;
  }
  .info-detail {
    margin-left: 2%;
  }
</style>